import { Dialog } from 'primereact/dialog';
import React, { useEffect, useState } from 'react';
import '../../scss/_dialog.scss';
import { Button } from 'primereact/button';
import heroesMarketService from 'services/heroesMarketService';

const BuyDialog = (props) => {
  const { dataDetail, visible } = props;
  const [step, setStep] = useState(1);
  const [error, setError] = useState(false);

  console.log('dataDetail', dataDetail);

  useEffect(() => {
    if (!visible) {
      setError(false);
      setStep(1);
    }
  }, [visible]);

  const buyHero = async () => {
    const respone = await heroesMarketService.buy(dataDetail.id, dataDetail.price);
    if (respone) {
      setStep(2);
    } else {
      setError(true);
    }
  };

  const _renderError = () => {
    return (
      <div className="error">
        <div className="error__title">Error</div>
        <div className="error__content">An error occurred</div>
      </div>
    );
  };

  const _renderByStep = () => {
    switch (step) {
      case 1:
        return (
          <div className="checkout">
            <div className="buy__title">Checkout</div>
            <div className="item__label">Item</div>
            <div className="checkout__item">
              <div className="item__img"></div>
              <div className="checkout__info item__info">
                <div className="info__name">Stas</div>
                <div className="info__owner">
                  <span className="info__label">Owner by: </span>
                  <b>Mr Jump</b>
                </div>
              </div>
              <div className="checkout__info info__price">
                <div className="price__bnb">0.005 BNB</div>
                <div className="price__usd">$ 37.73</div>
              </div>
            </div>
            <div className="checkout__total">
              <div className="item__label">Total</div>
              <div className="checkout__info info__price">
                <div className="price__bnb">0.005 BNB</div>
                <div className="price__usd">$ 37.73</div>
              </div>
            </div>
            <div className="checkout__btn">
              <div onClick={buyHero} className="btn">
                Checkout
              </div>
            </div>
          </div>
        );
      case 2:
        return (
          <div className="success">
            <div className="success__title">Success</div>
            <div className="success__icon"></div>
            <div className="success__info">
              <a href="/">View your profile</a> or <a href="/">back to all items</a>
            </div>
            <div className="success__view-bcs">
              <a href="/">View on BSC Scan</a>
            </div>
          </div>
        );
      case 3:
        return (
          <div className="add-bnb">
            <div className="buy__title">Add BNB to your wallet</div>
            <div className="add-bnb__desc">Select one of the options to deposit BNB to your wallet</div>
            <div className="add-bnb__options">
              <div className="option option__deposit">
                <div className="icon"></div>
                <div className="text">Deposit from an exchange</div>
              </div>
              <div className="option option__card">
                <div className="icon"></div>
                <div className="text">Buy with card</div>
              </div>
            </div>
          </div>
        );
      case 4:
        return (
          <div className="completing">
            <div className="completing__title">Completing checkout</div>
            <div className="completing__img"></div>
            <div className="completing__loading">
              <Button className="completing__button" loading />
            </div>
            <div className="completing__info">Waiting for blockchain comfirmation...</div>
          </div>
          //   <React.Suspense fallback={<h1>Loading posts...</h1>}>
          //   <React.Fragment>
          //     {isLoading ? (
          //       <div className="testspinner">
          //         <i className="pi pi-spin pi-spinner"></i>
          //       </div>
          //     ) : (
          //       <DataView />
          //     )}
          //   </React.Fragment>
          // </React.Suspense>
        );
      default:
        return null;
    }
  };
  return (
    <Dialog {...props} className="buy-dialog">
      {error ? _renderError() : _renderByStep()}
    </Dialog>
  );
};
export default BuyDialog;
