import { Dialog } from 'primereact/dialog';
import React, { useEffect, useState } from 'react';
import '../../scss/_dialog.scss';
import { Button } from 'primereact/button';
import { InputNumber } from 'primereact/inputnumber';
import heroesMarketService from 'services/heroesMarketService';

const ListingDialog = (props) => {
  const { dataDetail, visible } = props;
  const [error, setError] = useState(false);
  const [step, setStep] = useState(1);
  const [valuePrice, setValuePrice] = useState(0);
  const [isLoading, setLoading] = useState(false);

  useEffect(() => {
    if (!visible) {
      setError(false);
      setStep(1);
    }
  }, [visible]);

  const listingHero = async () => {
    setStep(2);
    try {
      const dataoffer = {
        id: dataDetail.id,
        price: valuePrice,
      };
      const respone = await heroesMarketService.listForSale(dataoffer);
      if (respone) {
        setStep(3);
      } else {
        setError(true);
      }
    } catch (error) {
      console.log('error', error);
    }
  };

  const _renderByStep = () => {
    switch (step) {
      case 1:
        return (
          <div className="list-hero">
            <div className="list__title">List your Hero</div>
            <div className="item__label">Price:</div>
            <div className="list-hero__item">
              <div className="list-hero__item-price-btn">
                <div className="list-hero__item-price-icon"></div>
                <span className="list-hero__item-price-label">BNB</span>
              </div>
              <InputNumber
                className="list-hero__item-price-input"
                inputId="minmax-buttons"
                value={valuePrice}
                onValueChange={(e) => setValuePrice(e.value)}
                mode="decimal"
              />
              <div className="list-hero__max-price">
                <span>MAX</span>
              </div>
            </div>
            <div className="list-hero__preview">{valuePrice} $</div>
            <div onClick={listingHero} className="list-hero__btn">
              <div className="btn">List</div>
            </div>
          </div>
        );
      case 2:
        return (
          <div className="completing">
            <div className="completing__title">Listing Hero</div>
            <div className="completing__loading">
              <Button className="completing__button" loading />
            </div>
            <div className="completing__info">Waiting for the transaction to be mined</div>
          </div>
        );
      case 3:
        return (
          <div className="success">
            <div className="success__title">Success</div>
            <div className="success__icon"></div>
            <div className="success__view-bcs">
              <a href="/">View on BSC Scan</a>
            </div>
          </div>
        );
      case 4:
        return (
          <div className="delist-hero">
            <div className="delist-hero__title">Delist your Hero</div>
            <div className="delist-hero__quest">Are you sure to delist your Hero from the marketplace?</div>
            <div className="delist-hero__options">
              <div className="option option__cancel">
                <div className="text">Cancel</div>
              </div>
              <div className="option option__delist">
                <div className="text">Delist</div>
              </div>
            </div>
          </div>
        );
      case 5:
        return (
          <div className="completing">
            <div className="completing__title">Delist your Hero</div>
            <div className="completing__loading">
              <Button className="completing__button" loading />
            </div>
            <div className="completing__info">Waiting for the transaction to be mined</div>
          </div>
        );
      case 6:
        return (
          <div className="success">
            <div className="success__title">Success</div>
            <div className="success__icon"></div>
            <div className="success__view-bcs">
              <a href="/">View on BSC Scan</a>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  const _renderError = () => {
    return (
      <div className="error">
        <div className="error__title">Error</div>
        <div className="error__content">An error occurred</div>
      </div>
    );
  };

  return (
    <Dialog {...props} className="listing-dialog">
      {error ? _renderError() : _renderByStep()}
    </Dialog>
  );
};
export default ListingDialog;
